import './Header.css';
import React from 'react';

function Header(props) {

    return (
        <header>
            <button className="home">THE PLANETS</button>
            <div className="buttonh">
                <button  id ="Mercury" onClick={props.onClick} value="Mercury" className={props.planet === "Mercury" ? "Mercury" : ""} >MERCURY</button>
                <button  id="Venus" onClick={props.onClick} className={props.planet === "Venus"? "Venus" : ""} value="Venus">VENUS</button>
                <button  id="Earth" onClick={props.onClick} value="Earth" className={props.planet === "Earth"? "Earth" : ""}>EARTH</button>
                <button  id="Mars" onClick={props.onClick} value="Mars" className={props.planet === "Mars"? "Mars" : ""}>MARS</button>
                <button  id="Jupiter" onClick={props.onClick} value="Jupiter" className={props.planet === "Jupiter"? "Jupiter" : ""}>JUPITER</button>
                <button  id="Saturn" onClick={props.onClick} value="Saturn" className={props.planet === "Saturn"? "Saturn" : ""}>SATURN</button>
                <button  id="Uranus" onClick={props.onClick} value="Uranus" className={props.planet === "Uranus"? "Uranus" : ""}>URANUS</button>
                <button  id="Neptune" onClick={props.onClick} value="Neptune" className={props.planet === "Neptune"? "Neptune" : ""}>NEPTUNE</button>
            </div>
        </header>
    )
}

export default Header;