import React from 'react';
import './Info.css';

function Information(props) {

    return (
        <div className="middle">

            <div className="planet-img">
                <img src={(props.img)} alt="oopps"></img>
                <img className="zoom" style={{ display: props.display }} src={(props.img_zoom)} alt="oops"></img>
            </div>

            <div className="info">
                <div className="planet-info">
                    <span className="title">{props.name}</span><br></br>
                    <span className="summary">{props.summary}</span>

                </div>
                <div className="buttons">
                    <button className={props.currentButton === "overview" ? props.planet : ""} onClick={props.onClick} id="overview" value="overview" >01 Overview</button>
                    <button className={props.currentButton === "structure" ? props.planet : ""} onClick={props.onClick} id="structure" value="structure">02 Internal Structure</button >
                    <button className={props.currentButton === "geology" ? props.planet : ""} onClick={props.onClick} id="geology" value="geology">03 Surface Geology</button >
                </div>
            </div>

           <div className="stats">

         
                <div className="stat">
                    <span className="data_title"> Rotation Time</span><br></br>
                    <span className="main-info">{props.rotation}</span>
                </div>

                <div className="stat">
                    <span className="data_title">Revolution Time</span><br></br>
                    <span className="main-info">{props.revolution}</span>
                </div>

                <div className="stat">
                    <span className="data_title">Radius</span><br></br>
                    <span className="main-info">{props.radius}</span>
                </div>

                <div className="stat">
                    <span className="data_title">Average Temp.</span><br></br>
                    <span className="main-info">{props.temperature}</span>
                </div>
           
                </div>
        </div>
    )
}

export default Information;